          position: 'Application Development Manager',
          company: 'Nordic Consulting Partners',
          description: 'Managing development and change management for NetSuite, Nexonia, Microsoft Azure and other Nordic systems. Planning, coordinating, and leading all activities related to the design, development, and implementation of Nordic’s business applications. Maintaining, supporting, and upgrading existing systems and applications',
