          position: 'Chief Business Development Officer',
          company: 'CapSquare Apps, llc',
          description: 'Has a broad and comprehensive knowledge of all matters related to the business of the organization with an eye towards identifying new sales prospects and driving business growth and requirements for product development that will be coordinated with R&D functions.',
